@font-face {
  font-family: "Choplin";
  src: url("../fonts/Rene Bieder - Choplin Medium-DEMO.otf") format("opentype");
}

@font-face {
  font-family: "Choplin-light";
  src: url("../fonts/32AAC6_0_0.woff") format("woff");
}

.header-picture {
  display: block;
  width: 100%;
  position: relative;
}

.header-picture .mySlides {
  width: 100%;
  z-index: 0;
}

.header-logo {
  position: absolute;
  z-index: 10;
  left: 50%;
  transform: translate(-50%, 50%);
  width: 20%;
}

#progressBar {
  background-color: white;
  height: 3px;
  position: absolute;
  bottom: 0px;
  z-index: 30;
  opacity: 0.4;
}

.home-button {
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  height: 120px;
  float: left;
  width: 16.66%;
  text-align: center;
  position: relative;
  border-right: 1px solid #616161;
}

.home-button .sub-title {
  position: absolute;
  bottom: 0;
  font-size: 20px;
  left: 0;
  right: 0;
  margin: auto;
  color: #ffffff;
}

.home-button .iconSprite {
  position: absolute;
  bottom: 50px;
  font-size: 20px;
  left: 0;
  right: 0;
  margin: auto;
}

.home-button:last-child {
  border-right: none;
}

.hot-item {
  background-color: #565656;
  border: 1px solid #717171;
  float: left;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  height: 210px;
}

.hot-item:hover div.hot-item-overlay {
  visibility: visible;
}

.hot-item .hot-item-image {
  width: 95%;
  height: 170px;
  overflow: hidden;
  position: relative;
  margin-left: auto;
  margin-right: auto;
}

.hot-item .hot-item-image .hot-item-overlay {
  position: absolute;
  z-index: 10;
  background-color: rgba(255, 111, 158, 0.7);
  width: 100%;
  height: 160px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-image: url("../images/informatilOverlay.png");
  background-repeat: no-repeat;
  background-position: center;
  visibility: hidden;
}

.hot-item .hot-item-image img {
  max-height: 160px;
  position: absolute;
  z-index: 0;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.hot-item .hot-item-info {
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 10px;
  padding-bottom: 10px;
  color: #ffffff;
  font-family: Arial;
  overflow: hidden;
}

.hot-item .hot-item-info .hot-item-name {
  float: left;
  margin: 0;
}

.hot-item .hot-item-info .hot-item-price {
  float: right;
  margin: 0;
}

.multiple-borders {
  position: relative;
  width: calc(24.25% - 13px) !important;
  height: 197px;
  margin-top: 13px;
}

.multiple-borders .hot-item-image {
  height: 152px;
}

.multiple-borders:before {
  content: " ";
  position: absolute;
  top: -14px;
  left: 14px;
  right: -14px;
  bottom: 0px;
  border-right: 5px solid #cfcfcf;
  border-top: 5px solid #cfcfcf;
}

.multiple-borders:after {
  content: " ";
  position: absolute;
  top: -7px;
  left: 7px;
  right: -7px;
  bottom: 0px;
  border-right: 5px solid #818181;
  border-top: 5px solid #818181;
}

.newsletter {
  clear: both;
  height: 188px;
}

.newsletter .newsletter-image {
  float: left;
  background-image: url("../images/patroon.jpg");
  background-repeat: repeat;
  height: 100%;
  width: 60%;
  text-align: center;
  color: #ffffff;
}

.newsletter .newsletter-image .newsletter-title {
  font-family: Choplin;
  font-size: 44px;
  margin-bottom: 0;
  margin-top: 25px;
}

.newsletter .newsletter-image .newsletter-subtitle {
  font-family: Choplin;
  font-size: 20px;
  margin-bottom: 0;
  margin-top: 25px;
}

.newsletter .newsletter-subscribe {
  float: left;
  width: 40%;
  height: 100%;
  background-color: #5f5f5f;
  padding: 35px;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  font-family: Arial;
}

.newsletter .newsletter-subscribe .subscribe-title {
  margin-top: 0px;
  color: #ffffff;
  font-size: 20px;
}

.newsletter .newsletter-subscribe .subscribe-subtitle {
  margin-top: 0px;
  color: #cecece;
  font-size: 14px;
}

.newsletter .newsletter-subscribe .newsletter-form {
  font-family: Arial;
  width: 100%;
}

.newsletter .newsletter-subscribe .newsletter-form .newsletter-email-input {
  padding: 12px;
  font-size: 16px;
  color: #000000;
  width: 70%;
  border: 1px solid #717171;
}

.newsletter .newsletter-subscribe .newsletter-form .newsletter-submit {
  padding: 12px;
  background-color: #323232;
  color: #ffffff;
  width: 20%;
  font-size: 16px;
  border: none;
}
