@font-face {
  font-family: "Choplin";
  src: url("../fonts/Rene Bieder - Choplin Medium-DEMO.otf") format("opentype");
}

@font-face {
  font-family: "Choplin-light";
  src: url("../fonts/32AAC6_0_0.woff") format("woff");
}

body {
  margin: 0;
}

#app {
  height: 1700px;
  background-color: #454545;
}

.navigation-bar {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 60;
  height: 1700px;
  float: left;
}

.content {
  margin-left: 60px;
  z-index: 10;
  overflow: hidden;
  height: 100%;
}

.content .content-store {
  padding-top: 50px;
  width: 960px;
  margin-left: auto;
  margin-right: auto;
}

.content .content-store .info-block {
  width: 100%;
  text-align: center;
}

.content .content-store .info-block p {
  font-family: Arial;
  font-size: 14px;
  color: #cecece;
  margin-top: 0px;
}

.content .content-store .homepage-navigation {
  margin-top: 50px;
  height: 120px;
}

.content .content-store .hot_items {
  margin-top: 70px;
  height: 260px;
}

.cookie-modal {
  position: relative;
  margin-left: auto;
  margin-right: auto;
  background-color: #f7eade;
  width: 430px;
  margin-top: 220px;
  padding-right: 80px;
  padding-left: 80px;
  padding-top: 40px;
  padding-bottom: 40px;
  box-sizing: border-box;
}
