@font-face {
  font-family: "Choplin";
  src: url("../fonts/Rene Bieder - Choplin Medium-DEMO.otf") format("opentype");
}

@font-face {
  font-family: "Choplin-light";
  src: url("../fonts/32AAC6_0_0.woff") format("woff");
}

.nav-option {
  width: 230px;
  height: 60px;
  position: relative;
  clear: both;
}

.nav-icon {
  float: left;
  width: 60px;
  height: 100%;
  position: relative;
}

.nav-icon .sprite {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  margin: 0 auto;
  left: 0;
  right: 0;
}

.nav-info {
  height: 100%;
  float: left;
  font-family: Arial;
  font-size: 22px;
  color: white;
  padding-top: 20px;
  padding-left: 15px;
}

.nav-info p {
  margin: 0px;
}

#hamburger {
  background-color: #252525;
  margin: 0px;
}

.store-name {
  width: 100%;
  position: absolute;
  bottom: 0;
  text-align: center;
}

.store-name p {
  font-size: 30px;
  color: #616161;
  font-family: Choplin-light;
}
