.sprite-cookie {
  background-image: url("../images/cookie-spritesheet.png");
  background-repeat: no-repeat;
  display: block;
}

.sprite-X {
  width: 17px;
  height: 16px;
  background-position: -5px -5px;
  position: absolute;
  right: 20px;
  top: 20px;
}

.sprite-cookieCrumble {
