@font-face {
  font-family: "Choplin";
  src: url("../fonts/Rene Bieder - Choplin Medium-DEMO.otf") format("opentype");
}

@font-face {
  font-family: "Choplin-light";
  src: url("../fonts/32AAC6_0_0.woff") format("woff");
}

.landingPageIntro {
  font-family: Arial;
  color: #cecece;
}

.title {
  font-family: Choplin;
  color: #ffffff;
}

.sub-title {
  font-family: Choplin-light;
}

.info-text {
  font-family: Arial;
}

.overlay-title {
  font-family: Choplin;
  font-size: 40px;
  color: #e27500;
}

.faq-customer-information {
  margin-top: 55px;
  color: #535353;
  font-size: 14px;
  font-family: Arial;
  margin-bottom: 40px;
}

.faq-customer-information p {
  margin: 0;
}

.faq-h {
  font-size: 20px;
  font-family: Arial;
  margin: 0;
  font-weight: bold;
}

.faq-p {
  font-size: 20px;
  font-family: Arial;
  font-size: 14px;
  margin-left: 20px;
}

.cookie-title {
  font-family: Choplin;
  font-size: 40px;
  color: #e27500;
  margin-bottom: 0px;
  margin-top: 40px;
}

.cookie-info {
  line-height: 20px;
  font-family: Arial;
  font-size: 15px;
  color: #454545;
}
