@font-face {
  font-family: "Choplin";
  src: url("../fonts/Rene Bieder - Choplin Medium-DEMO.otf") format("opentype");
}

@font-face {
  font-family: "Choplin-light";
  src: url("../fonts/32AAC6_0_0.woff") format("woff");
}

.navigation-bar {
  width: 60px;
  overflow: hidden;
  transition: width 0.2s;
  background-color: #323232;
}

.expand-navigation-bar {
  width: 230px;
}

.navigation-options {
  box-sizing: border-box;
  padding-bottom: 15px;
  border-bottom: 1px solid #616161;
}

.navigation-options .nav-option:first-child .nav-icon {
  background-color: #252525;
}

.navigation-options .nav-option:first-child .nav-icon:hover {
  background: none;
}

.navigation-options p {
  color: #616161;
}

.navigation-animals {
  box-sizing: border-box;
  padding-top: 15px;
}

.homepage-navigation {
  width: 960px;
}

.hot_items {
  width: 960px;
}

.hot_items .hot-item {
  margin-right: 1%;
  width: 24.25%;
}

.hot_items .hot-item:last-child {
  margin-right: 0px;
}
